import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import movie from '@/components/Movie'
import book from '@/components/book'
import broadcast from '@/components/broadcast'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/movie',
      name: 'movie',
      component: movie
    },
    {
      path: '/book',
      name: 'book',
      component: book
    },
    {
      path: '/broadcast',
      name: 'broadcast',
      component: broadcast
    }
  ]
})



